import "../index.css"
import { Link } from "react-router-dom"
import styles from "../Assets/css/Inscricoes.module.css"
import Sino from "../Assets/img/img projetos/Sino.png"

export default function Inscricoes()
{
    return(
        <>
            {/* Nome Portal inscri */}
            <nav className="menu">
                <Link to="/portal_artistas"><div className="seta"></div></Link>
                <h1 className="titulo1">Portal inscries</h1>
                <Link to="/notificacao"><img className="sino1" src={Sino} alt="Sino de notificao" /></Link>
            </nav>

            {/* Meio ou barriga da pagna */}
            <section className={styles.corpo}>

                {/* Inscries */}
                <div className={styles.inscricoes}>
                    <div className={styles.container_inscricoes}>
                        <h1 className={styles.titulo}>INSCRIES</h1>

                        <p className={styles.texto}>Inscreva-se para participar do nosso processo seletivo. </p>

                        <hr className={styles.hr_inscricao}></hr>

                        <p className={`${styles.editais}`}>EDITAL N 24/2024-CULTURA+/PB - EXAME DE SELEO</p>

                        <p className={styles.texto}>Periodo de Incrio</p>

                        <p className={styles.texto}>07 Agosto de  2024 at 08 Setembro de 2024 </p>

                        <Link to="/editais"><input className={styles.botao_laranja} type="button" value="Visualizar"/></Link>
                        
                        <hr className={styles.hr_inscricao}></hr>

                    </div>
                </div>

                {/* Minhas inscries */}
                <div className={styles.inscricoes}>
                    <div className={styles.container_inscricoes}>

                        <h2 className={styles.titulo}>MINHAS INCRIES</h2>

                        <p className={styles.texto}>Participaes em Editais</p>

                        <hr className={styles.hr_minhas_inscricao}></hr>

                        <p className={styles.editais}>EDITAL N 24/2024-CULTURA+/PB - EXAME DE SELEO</p>

                        <hr className={styles.hr_inscricao}></hr>

                        <input className={styles.botao_laranja_minhas} type="button" value="Visualizar"/>
                    </div>
                </div>

                {/* Novos */}
                <div className={`${styles.inscricoes} ${styles.novo}`}>
                    <div className={styles.container_inscricoes}>
                        <h3 className={styles.titulo}>NOVOS</h3>

                        <p className={styles.texto}>Conhea prepara-se para novos processos seleo.</p>

                        <hr></hr>

                        <div className={styles.container_novos}>
                            <p className={styles.texto}>No a novos processos selees.</p>
                        </div>
                    </div>

                    <input className={styles.botao_andamento} type="button" value="Em Andamento"/>

                    <Link to="/feedback"><input className={`${styles.botao_andamento} ${styles.feedback}`} type="button" value="Feedback"/></Link>
                </div>
            </section>

            {/* Imagem do rodap */}
            <footer className={styles.imagem_rodape}></footer>
        </>
    )
}