import styles from "../Assets/css/editais.module.css"
import Sino from "../Assets/img/img projetos/Sino.png"
import { Link } from "react-router-dom"

export default function Editais()
{
    return (
        <div className={styles.corpo}>
            {/* Nome Portal inscri */}
            <nav className="menu">
                <Link to="/inscricoes"><div className="seta"></div></Link>
                <h1 className={`titulo1 ${styles.titulo}`}>Portal inscries</h1>
                <Link to="/notificacao"><img className="sino1" src={Sino} alt="Sino de notificao" /></Link>
            </nav>

            <div className={styles.fundo}></div>

            <div className={styles.area_textos}>
                <h1 className={styles.titulo_noticia}>Notcia</h1>

                <h1 className={styles.sub_titulo}>Cultura+ PB Anuncia Novo Exame de Seleo: Abertura das Inscries para 2024</h1>

                <p className={styles.paragrafos}>A to aguardada abertura do Exame de Seleo do Programa Cultura+ da Secretaria de Cultura do Estado da Paraba chegou! O Edital N 24/2024 foi publicado e marca o incio do processo seletivo que visa selecionar iniciativas culturais de destaque para o ano de 2024.</p>

                <h1 className={styles.sub_titulo}>Oportunidades para 2024</h1>

                <p className={styles.paragrafos}>O presente edital tem como objetivo selecionar 100 projetos culturais inovadores e 50 bolsas de estudo em reas culturais, com o intuito de promover e apoiar a criatividade e a excelncia cultural no estado da Paraba. O foco  incentivar a realizao de projetos que tragam novas perspectivas culturais e oferecer suporte educacional a talentos emergentes nas artes e reas relacionadas.</p>

                <h1 className={styles.sub_titulo}>Perodo de Inscrio</h1>

                <p className={styles.paragrafos}> As inscries para o exame de seleo estaro abertas de [data de incio] at [data de trmino]. </p>

                <p className={styles.editais}>Confira os editais:</p>

                <Link to="/cadastro-edital"><input className={styles.botao} type="button" value="Cultura+ PB" /></Link>
            </div>
        </div>
    )
}