import styles from "../Assets/css/Projetos.module.css"
import Lupa from "../Assets/img/img projetos/Lupa.png"
import Sino from "../Assets/img/img projetos/Sino.png"
import Blocos_de_projetos from "./blocos_de_projetos.js"
import { useState, useEffect } from "react"
import { Link } from "react-router-dom"
import "../index.css"

export default function Projetos()
{

    const [blocos_proj, setBlocos_proj] = useState([])

    function Repete() {
                
        useEffect(() => {
          // Simulando um delay de 5 segundos antes de executar o fetch
          const timer = setTimeout(() => {
            fetch(`http://localhost:3001/get_project?id=${localStorage.getItem("usuario_id")}`)
              .then(response => response.json())
              .then(dados => {
                const novosBlocos = [];
      
                for (let i = 0; i < dados.length; i++) {
                  novosBlocos.push(<Blocos_de_projetos key={i} titulo={dados[i].Titulo} descricao={dados[i].Descricao} />);
                }
      
                setBlocos_proj(novosBlocos); // Atualiza o estado com os novos blocos
              });
          }, 50); // Executa o fetch aps 5 segundos
      
          // Funo de limpeza caso o componente seja desmontado antes do timeout ou fetch serem concludos
          return () => clearTimeout(timer);
        }, []); // O array vazio [] garante que o efeito seja executado apenas uma vez aps a montagem
      
        return <div>{blocos_proj}</div>;
      }

    return(
        <div>
            <nav className="menu">
                <Link to="/portal_artistas"><div className="seta"></div></Link>
                <h1 className={styles.titulo}>Histrico de Projetos</h1>
                <Link to="/notificacao"><img className={styles.sino} src={Sino} alt="Sino de notificao" /></Link>
            </nav>

            <section className={styles.corpo}>
                <div className={styles.corpo_p1}>

                    {/* Box 1 */}

                    <div className={styles.box}>
                        <div className={styles.box_nav}>
                            <img className={styles.lupa} src={Lupa} alt="Lupa"/>
                            <p className={styles.box_titulo}>Titulo: Arte Digital e Novas Mdias </p>
                        </div>

                        <p className={styles.box_texto}> Explorao de novas tecnologias e mdias digitais para criar arte interativa, arte generativa, realidade aumentada, entre outros.</p>
                    </div>

                    {/* Box 2 */}

                    <div className={styles.box}>
                        <div className={styles.box_nav}>
                            <img className={styles.lupa} src={Lupa} alt="Lupa"/>
                            <p className={styles.box_titulo}>Ttulo: Arte Ambiental </p>
                        </div>

                        <p className={styles.box_texto}> Criao de obras que respondem ao ambiente natural, utilizando materiais encontrados localmente ou abordando questes ambientais.</p>
                    </div>

                    {/* Box 3 */}

                    <div className={styles.box}>
                        <div className={styles.box_nav}>
                            <img className={styles.lupa} src={Lupa} alt="Lupa"/>
                            <p className={styles.box_titulo}>Titulo:Residncias Artsticas </p>
                        </div>

                        <p className={styles.box_texto}>Participao em programas de residncia onde artistas vivem e trabalham juntos por um perodo especfico de tempo, colaborando e produzindo arte.</p>
                    </div>

                    {/* <Blocos_de_projetos titulo="oi" descricao="ol"/> */}
                    {Repete()}

                    {/* Criar um projeto */}

                    <Link to="/cadastro"><input className={styles.btn_criar_proj} value="Criar um novo projeto" type="button"/></Link>
                </div>

                <div className={`${styles.corpo_p1} ${styles.corpo_p2}`}></div>
            </section>
        </div>
    )
}