import React from 'react';
import ReactDOM from 'react-dom/client';
import Home from './Home.js';
import Login from './pagnas/login.js'
import Criar_conta from './pagnas/criar_conta.js';
import Recuperar_senha from './pagnas/recuperar_senha.js';
import Portal_artista from './pagnas/Portal artista.js';
import Projetos from './pagnas/Projetos.js';
import Inscricoes from './pagnas/Inscricoes.js';
import Editais from './pagnas/editais.js';
import Inscries_do_projeto from './pagnas/inscries_do_projeto.js';
import Cadrasto_de_proj from './pagnas/cadrasto_de_proj.js';
import Feedback from './pagnas/feedback.js';
import Notificacao from "./pagnas/notificacao.js"
import { createBrowserRouter, RouterProvider } from 'react-router-dom';
import reportWebVitals from './reportWebVitals.js';

const rotas = createBrowserRouter([
  {
    path: "/",
    element: <Home/>
  },
  {
    path: "/login",
    element: <Login/>
  },
  {
    path: "/criar_conta",
    element: <Criar_conta/>
  },
  {
    path: "/recuperar_senha",
    element: <Recuperar_senha/>
  },
  {
    path: "/portal_artistas",
    element: <Portal_artista/>
  },
  {
    path: "/inscricoes",
    element: <Inscricoes/>
  },
  {
    path: "/editais",
    element: <Editais/>
  },
  {
    path: "/cadastro-edital",
    element: <Inscries_do_projeto/>
  },
  {
    path: "/cadastro",
    element: <Cadrasto_de_proj/>
  },
  {
    path: "/feedback",
    element: <Feedback/>
  },
  {
    path: "/projetos",
    element: <Projetos/>
  },
  {
    path: "/notificacao",
    element: <Notificacao/>
  }
])

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <RouterProvider router={rotas} />
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
